<template>
  <v-footer
    style="width: 100% !important; "
    :class="'footer'"
  >
    <v-card
      flat
      tile
      color="#525252"
      style="color: #adadad; background: transparent; width: 100%; width: 100% !important; margin: 0px!important; padding: 0px !important;"
    >
      <v-row class="row row__footer pt-2 pb-2">
        <v-col
          v-animate-onscroll.repeat="{ down: 'animated bounceIn' }"
          md="2"
          sm="12"
          class="m-0 text-center pt-8"
        >
          <v-btn
            v-for="(rs, i) in rrss"
            :key="i"
            class="mx-0 white--text"
            icon
            style="color: #adadad;"
            :href="rs.href"
            target="_blank"
          >
            <v-icon size="24px">
              {{ rs.icon }}
            </v-icon>
          </v-btn>
        </v-col>
        <v-col
          md="9"
          sm="12"
        >
          <v-row class="row row__suc">
            <v-col
              v-for="(item, index) in sucursales"
              :key="index"
            >
              <v-row class="row__suc__desg">
                <v-col
                  md="2"
                  sm="12"
                >
                  <v-btn icon>
                    <v-icon
                      size="40px"
                      style="color: #adadad;"
                    >
                      {{ 'mdi-map-marker' }}
                    </v-icon>
                  </v-btn>
                </v-col>
                <v-col md="10">
                  <p>
                    <b>{{ item.Nombre }}</b><br>
                    {{ item.DireccionCompleta }} <br>
                    {{ item.Email }} <br>
                    <v-icon>mdi-phone</v-icon>
                    <a :href="`tel: ${item.Telefono}`">{{ item.Telefono }}</a>
                  </p>
                </v-col>
              </v-row>
            </v-col>
          </v-row>
        </v-col>
        <v-col
          md="1"
          class="col-btn-subir"
        >
          <v-btn
            v-animate-onscroll.repeat="{ down: 'animated bounceIn' }"
            class="btn_subir"
            small
            @click="scrollToTop"
          >
            <v-icon
              style="padding: 0px;"
              x-large
            >
              mdi-menu-up
            </v-icon>
          </v-btn>
        </v-col>
      </v-row>

      <v-divider style="border: 0px;" />

      <v-container
        style="background-color: #51575c; margin: 0px !important; font-size: 16px !important;"
        fluid
      >
        <v-row class="row row__last pl-7 pr-7">
          <v-col class="col__copyright">
            {{ new Date().getFullYear() }} |
            <span>
              Todos los derechos reservados -
              <router-link
                style="text-decoration: none; color: #adadad; padding: 0px; margin: 0px;"
                to="/admin/login"
              >
                CETEC
              </router-link>
            </span>
          </v-col>
          <v-col
            class="col__boletas"
            style="text-align: right;"
          />
        </v-row>
      </v-container>
    </v-card>
  </v-footer>
</template>

<script>
  import { mapState } from 'vuex'

  export default {
    computed: {
      ...mapState('web', ['sucursales', 'rrss']),
    },
    methods: {
      scrollToTop () {
        window.scrollTo({ top: 0, behavior: 'smooth' })
      },
    },
  }
</script>
